import { BaseComp } from '../../lib/components/base-comp/base-comp';
import { REST_CLIENT_GARAGE } from '../../services/rest-clients/client-garage/rest-client-garage';
import { REST_CLIENT_WINNERS } from '../../services/rest-clients/client-winners/rest-client-winners';
import {
  RestClientWinnersSortOptionsList,
  REST_CLIENT_WINNERS_SORT_OPTIONS,
} from '../../services/rest-clients/client-winners/rest-client-winners.def';
import { WinnerRowComp } from './subcomps/winner-entry';
import { Pager } from '../../lib/services/pager/pager';

import styles from './winners.scss';
import sharedStyles from '../../styles.scss';
import { Notifiable } from '../../lib/interfaces/notifiable';

export class WinnersComp extends BaseComp implements Notifiable {
  static readonly TEXT_CONTENT_ENG = {};

  private table = document.createElement('table');

  private tableBody = document.createElement('tbody');

  private tableHead = document.createElement('thead');

  private thId = document.createElement('th');

  private thCar = document.createElement('th');

  private thName = document.createElement('th');

  private thWins = document.createElement('th');

  private thBestTime = document.createElement('th');

  private pager: Pager;

  private pagerBtnsContainer = document.createElement('div');

  private pagerSpan: HTMLSpanElement;

  private currSortedElem: HTMLElement | undefined;

  private currOrder: 'ASC' | 'DESC' = 'ASC';

  private currSort: RestClientWinnersSortOptionsList = 'time';

  private entriesPerPage = 10;

  constructor() {
    super('section');
    this._rootElement.classList.add(styles.winners);
    this._rootElement.style.setProperty(
      'background-image',
      `url('./assets/imgs/backgrounds/winners/anime-girl-cyberpunk-ride-4k-lx-2880x1800.jpg')`,
    );

    this.pager = new Pager(this.entriesPerPage, this.pagerCallback);
    this.pagerSpan = this.pager.getPagerSpan();

    this.renderPager();

    this.renderTable();

    this._rootElement.append(this.table);
  }

  private renderTable() {
    this.renderTableHeader();
    this.table.classList.add(styles.table);
    this.table.append(
      this.tableHead,
      this.tableBody,
      this.pagerBtnsContainer,
      this.pagerSpan,
    );
  }

  private renderTableHeader() {
    const tr = document.createElement('tr');

    const thIndex = document.createElement('th');
    thIndex.textContent = '‚Ññ';
    this.thId.textContent = 'ID';
    this.thId.classList.add(styles['table__can-be-sorted']);
    this.thCar.textContent = 'CAR';
    this.thName.textContent = 'NAME';
    this.thWins.textContent = 'WINS';
    this.thWins.classList.add(styles['table__can-be-sorted']);
    this.thBestTime.textContent = 'BEST TIME';
    this.thBestTime.classList.add(styles['table__can-be-sorted']);

    tr.append(
      thIndex,
      this.thId,
      this.thCar,
      this.thName,
      this.thWins,
      this.thBestTime,
    );
    this.tableHead.append(tr);
    this.tableHead.addEventListener('click', this.tableHeadClickListener);
  }

  private renderPager() {
    const btnPrevPage = this.pager.getBtnPrevPage();

    btnPrevPage.classList.add(
      sharedStyles.btn,
      sharedStyles['btn-6'],
      styles['pager-btn'],
    );

    const btnNextPage = this.pager.getBtnNextPage();

    btnNextPage.classList.add(
      sharedStyles.btn,
      sharedStyles['btn-6'],
      styles['pager-btn'],
    );

    this.pagerBtnsContainer.classList.add(styles.pager);
    this.pagerBtnsContainer.append(btnPrevPage, btnNextPage);

    this.pagerSpan.classList.add(styles['pager-span']);
  }

  notify(): void {
    this.refillTable().then(() =>
      this.refreshTableHeader(this.currSortedElem || this.thBestTime),
    );
  }

  private pagerCallback = async (dir: 'prev' | 'next'): Promise<boolean> => {
    try {
      await this.refillTable(dir);
      return true;
    } catch {
      return false;
    }
  };

  private async refillTable(dir?: 'prev' | 'next'): Promise<void> {
    return new Promise((res, rej) => {
      const page = dir
        ? dir === 'next'
          ? this.pager.getCurrPageNumber() + 1
          : this.pager.getCurrPageNumber() - 1
        : this.pager.getCurrPageNumber();

      console.log(page, this.currSort, this.currOrder);
      REST_CLIENT_WINNERS.getWinners(
        page,
        10,
        this.currSort,
        this.currOrder,
      ).then(async response => {
        this.pager.setEntriesCount(Number.parseInt(response.limit, 10));
        if (response.cars.length === 0) {
          if (dir) {
            rej();
            return;
          }

          if (page > 1) {
            await this.pager.tryChangePage('prev');
            res();
            return;
          }
        }
        const rows = new Array<WinnerRowComp>(response.cars.length);
        const promises: Promise<unknown>[] = [];
        const indexInc = (page - 1) * 10;
        response.cars.forEach((winner, index) => {
          const promise = REST_CLIENT_GARAGE.getCar(winner.id).then(car => {
            const totalIndex = index + indexInc + 1;
            const row = new WinnerRowComp(totalIndex, winner, car);
            console.log(totalIndex, winner.time);
            rows[index] = row;
          });
          promises.push(promise);
        });
        await Promise.allSettled(promises);
        while (this.tableBody.firstChild) this.tableBody.firstChild.remove();
        this.tableBody.append(
          ...rows.map(row => {
            row.getCarPreview().classList.add(styles['table__car-preview-box']);
            return row.rootElement;
          }),
        );
        res();
      });
    });
  }

  private tableHeadClickListener = async (e: Event) => {
    let sort: RestClientWinnersSortOptionsList | undefined;
    switch (e.target) {
      case this.thId:
        sort = REST_CLIENT_WINNERS_SORT_OPTIONS.id;
        break;
      case this.thWins:
        sort = REST_CLIENT_WINNERS_SORT_OPTIONS.wins;
        break;
      case this.thBestTime:
        sort = REST_CLIENT_WINNERS_SORT_OPTIONS.time;
        break;
      default:
        break;
    }
    if (sort) {
      this.pager.toogleLock(true);
      const elem = e.target as HTMLElement;
      this.currOrder = elem.classList.contains(
        styles['table__sorted-elem_desc'],
      )
        ? 'ASC'
        : 'DESC';
      this.currSort = sort;
      await this.refillTable();
      this.refreshTableHeader(elem);
      this.pager.toogleLock(false);
    }
  };

  private refreshTableHeader = (elem: HTMLElement) => {
    if (this.currSortedElem)
      this.currSortedElem.classList.remove(styles['table__sorted-elem']);

    elem.classList.add(styles['table__sorted-elem']);
    this.currSortedElem = elem;

    if (this.currOrder === 'ASC') {
      elem.classList.remove(styles['table__sorted-elem_desc']);
      elem.classList.add(styles['table__sorted-elem_asc']);
    } else {
      elem.classList.remove(styles['table__sorted-elem_asc']);
      elem.classList.add(styles['table__sorted-elem_desc']);
    }
  };
}
