import { BaseComp } from '../lib/components/base-comp/base-comp';
import { SimpleRouterAgent } from '../lib/services/simple-router/simple-router-agent';
import { SimpleRouter } from '../lib/services/simple-router/simple-router';
import { DBus } from '../lib/services/dbus/dbus';
import { DBusMsgRouteIsChangedMsg } from '../lib/services/simple-router/dbus-msgs-defs/types/dbus-msg-route-is-chaned-msg';
import { DBusTopics } from '../lib/services/dbus/defs/dbus-topics.def';
import { DBUS_COMMON_RESPONSES } from '../lib/services/dbus/defs/dbus-common-responses.def';
import { HeaderComponent } from '../components/header/header';

import sharedStyles from '../styles.scss';
import { DBusTXData } from '../lib/services/dbus/defs/dbus-tx/dbus-tx-data.def';
import { GarageComp } from '../components/garage/garage';
import {
  DBusHub,
  isDBusHub,
  DBusInterface,
} from '../lib/services/dbus/defs/dbus-interface.def';
import { DBUS_HUB_NAMES } from '../lib/services/dbus/defs/dbus-hub-names.def';
import { WinnersComp } from '../components/winners/winners';
import { REST_CLIENT_ERROR } from '../services/rest-clients/rest-clients-errors.def';
import { isNotifiable } from '../lib/interfaces/notifiable';
import { PopupStandaloneModal404Comp } from '../lib/components/popups/standalone/modal/404/popup-404';
import { REST_CLIENT_GARAGE } from '../services/rest-clients/client-garage/rest-client-garage';
import { AppFlags } from './app-flags';
import { DBusMsgRebootMsg } from './dbus-msgs/reboot-msg.def';
import { DBusTX } from '../lib/services/dbus/defs/dbus-tx/dbus-tx.def';
import {
  DBusTXPrepModes,
  DBusTXWaitModes,
} from '../lib/services/dbus/defs/dbus-tx/dbus-tx-modes.def';

export class App implements DBusHub {
  readonly hubName = DBUS_HUB_NAMES.APP;

  protected loadedMainComponents: Record<string, BaseComp> = {};

  protected currMainComponent: BaseComp | undefined;

  protected defaultRouterAgent = new SimpleRouterAgent('hash', '', () =>
    this.loadMainComponent('default'),
  );

  protected router = new SimpleRouter({
    mode: '#',
    defaultRoot: '',
    defaultAgent: this.defaultRouterAgent,
  });

  protected mainlineDBus = new DBus();

  protected tx: DBusTX | undefined;

  protected rebootMsg = new DBusMsgRebootMsg('reboot');

  protected emergencyHandlerIsWorking = false;

  constructor(private readonly rootElement: HTMLElement) {
    // window.addEventListener('error', e => console.log(e));
    this.setupEmergencyHandler();

    this.rootElement.classList.add(sharedStyles.app);

    const header = new HeaderComponent(DBUS_HUB_NAMES.HEADER);
    this.rootElement.append(header.rootElement);

    this.mainlineDBus.attachHub(this);
    this.mainlineDBus.attachHub(header);
    this.mainlineDBus.attachHub(this.router);

    this.router.activateWantedAgents();
  }

  private setupEmergencyHandler() {
    window.addEventListener('unhandledrejection', e => {
      e.preventDefault();
      e.promise.catch((error: Error) => {
        if (
          !this.emergencyHandlerIsWorking &&
          error.message in REST_CLIENT_ERROR &&
          error.message === REST_CLIENT_ERROR.CONNECTION_REFUSED
        ) {
          this.emergencyHandlerIsWorking = true;
          const popup = new PopupStandaloneModal404Comp();
          popup
            .setText(
              `No connection! Check that the server is online
          and nothing is blocking the connection!`,
            )
            .show();
          const retry = async () => {
            try {
              await REST_CLIENT_GARAGE.getCars();
              console.log('SEND MSG!');
              await this.sendRebootMsg();
              console.log('HIDDING');
              popup.hide();
              this.emergencyHandlerIsWorking = false;
            } catch {
              console.log('catched');
              await new Promise(res => setTimeout(res, 1000));
              retry();
            }
          };
          retry();
        }
      });
    });
  }

  private sendRebootMsg = () => {
    return this.tx?.transmit({
      msg: this.rebootMsg,
      prepMode: DBusTXPrepModes.freeze,
      waitMode: DBusTXWaitModes.allSetled,
    });
  };

  connect(intrfce: DBusInterface) {
    const routeRX = intrfce.getNewRX(this.watchRouterMsgs);
    intrfce.plugRX(DBusTopics.ROUTING, routeRX);
    this.tx = intrfce.getNewTX('APP SYSTEM MSGS TX');
  }

  private watchRouterMsgs = async (data: DBusTXData) => {
    const { msg } = data;
    if (msg instanceof DBusMsgRouteIsChangedMsg) {
      // TODO: Rework with enum!?
      this.loadMainComponent(msg.newRoute);
    }
    return DBUS_COMMON_RESPONSES.VOID;
  };

  private loadMainComponent(name: string): void {
    if (!(name in this.loadedMainComponents)) {
      switch (name) {
        case '#garage':
          this.loadedMainComponents[`${name}`] = new GarageComp();
          break;
        case '#winners':
          this.loadedMainComponents[`${name}`] = new WinnersComp();
          break;
        default:
          window.location.hash = '#garage';
          break;
      }
    }
    const newComponent = this.loadedMainComponents[`${name}`];

    if (newComponent) {
      if (this.currMainComponent) {
        this.currMainComponent.rootElement.remove();
      }

      this.currMainComponent = newComponent;
      newComponent.rootElement.classList.add(sharedStyles['main-component']);
      this.rootElement.append(newComponent.rootElement);

      if (isDBusHub(this.currMainComponent)) {
        try {
          this.mainlineDBus.attachHub(this.currMainComponent);
        } catch {
          // no need to catch
        }
      }
      if (isNotifiable(this.currMainComponent)) {
        console.log('notify');
        this.currMainComponent.notify(null);
      }
    }
  }
}
